div {
  color: #fff;
  font-size: 2rem;
}
div h1 {
  margin: 0 0 40px 0;
}
div .select-operation button {
  border: none;
  outline: none;
  width: 49.5%;
  margin: 0 1% 0 0;
  padding: 30px;
  cursor: pointer;
  font-size: 2rem;
  background: #e2e2e2;
  background: -webkit-gradient(linear, left top, left bottom, from(#e2e2e2), color-stop(50%, #dbdbdb), color-stop(51%, #d1d1d1), to(#fefefe));
  background: linear-gradient(to bottom, #e2e2e2 0%, #dbdbdb 50%, #d1d1d1 51%, #fefefe 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr="#e2e2e2", endColorstr="#fefefe",GradientType=0 );
}
div .select-operation button:last-child {
  margin: 0;
}
div .select-operation button:hover {
  background: rgba(255, 255, 255, 0.685);
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(226, 226, 226, 0.685)), color-stop(50%, rgba(219, 219, 219, 0.685)), color-stop(51%, rgba(209, 209, 209, 0.685)), to(rgba(254, 254, 254, 0.685)));
