.atm {
  background-color: #4F86C6;
  min-height: 100vh;
  font-family: "Lato", sans-serif;
}
.atm .atm-menu {
  width: 800px;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-right: -50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  padding: 10px 0;
}
