.operation .operation-box {
  padding: 20px 10px;
  display: flex;
  align-items: flex-start;
  background: #17301C;
  justify-content: space-between;
}
.operation .operation-box .show-balance {
  text-align: right;
  width: 50%;
  padding: 0 10px 0 0;
}
.operation .operation-box .show-balance h2 {
  margin: 0;
}
.operation .operation-box .show-balance p {
  margin: 0;
}
.operation .operation-box .operation-type {
  text-align: left;
  width: 50%;
  padding: 0 0 0 10px;
}
.operation .operation-box .operation-type h2 {
  margin: 0;
}
.operation .screen-box {
  display: flex;
  justify-content: space-between;
  background-color: #379392;
  align-items: center;
  padding: 10px;
}
.operation .screen-box .funds-text {
  font-size: 1.5rem;
  padding: 0 0 0 10px;
}
.operation .screen-box .funds-text p {
  margin: 10px 0;
}
.operation .screen-box .balance {
  padding: 0 10px 0 0;
}
.operation .screen-box .balance p {
  margin: 10px 0;
}
.operation .amount-select .amount-row {
  display: flex;
  flex-direction: column;
  flex-flow: row wrap;
  justify-content: space-between;
}
.operation .amount-select .amount-row .amount {
  flex-grow: 1;
  flex-shrink: 0;
