  background: rgba(255, 255, 255, 0.685);
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(226, 226, 226, 0.685)), color-stop(50%, rgba(219, 219, 219, 0.685)), color-stop(51%, rgba(209, 209, 209, 0.685)), to(rgba(254, 254, 254, 0.685)));
