.screen {
  background-color: #4FB0C6;
  width: 500px;
  margin: 0 auto;
  padding: 30px;
}
.screen h2 {
  margin: 0;
  font-size: 2.5rem;
}

.buttons {
  width: 560px;
  margin: 0 auto;
}
.buttons .buttons-row {
  display: flex;
  justify-content: flex-start;
}
.buttons .buttons-row button {
  width: 180px;
  padding: 15px 0;
  font-size: 2.5rem;
  color: #000329;
  cursor: pointer;
  margin: 10px 10px 0 0;
  background: #e2e2e2;
  background: -moz-linear-gradient(top, #e2e2e2 0%, #dbdbdb 50%, #d1d1d1 51%, #fefefe 100%);
  background: -webkit-linear-gradient(top, #e2e2e2 0%, #dbdbdb 50%, #d1d1d1 51%, #fefefe 100%);
  background: linear-gradient(to bottom, #e2e2e2 0%, #dbdbdb 50%, #d1d1d1 51%, #fefefe 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr="#e2e2e2", endColorstr="#fefefe",GradientType=0 );
  border: none;
  outline: none;
}
.buttons .buttons-row button:last-child {
  margin: 10px 0 0 0;
}
.buttons .buttons-row button:hover {
  background: rgba(255, 255, 255, 0.685);
  background: -moz-linear-gradient(top, #e2e2e2 0%, #dbdbdb 50%, #d1d1d1 51%, #fefefe 100%);
  background: -webkit-linear-gradient(top, #e2e2e2 0%, #dbdbdb 50%, #d1d1d1 51%, #fefefe 100%);
  background: linear-gradient(to bottom, rgba(226, 226, 226, 0.685) 0%, rgba(219, 219, 219, 0.685) 50%, rgba(209, 209, 209, 0.685) 51%, rgba(254, 254, 254, 0.685) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr="#e2e2e2", endColorstr="#fefefe",GradientType=0 );
}
.buttons .buttons-row .back-button {
  background: linear-gradient(to bottom, #ffe61d 0%, #ffe61d 50%, #f5dc13 51%, #f5dc13 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
}
.buttons .buttons-row .back-button:hover {
  background: linear-gradient(to bottom, rgba(255, 230, 29, 0.7) 0%, rgba(255, 230, 29, 0.7) 50%, rgba(245, 220, 19, 0.7) 51%, rgba(245, 220, 19, 0.7) 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
}
.buttons .buttons-row .delete-button {
  background: linear-gradient(to bottom, #ff1d1d 0%, #ff1d1d 50%, #f51313 51%, #f51313 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
}
.buttons .buttons-row .delete-button:hover {
  background: linear-gradient(to bottom, rgba(255, 29, 29, 0.7) 0%, rgba(255, 29, 29, 0.7) 50%, rgba(245, 19, 19, 0.7) 51%, rgba(245, 19, 19, 0.7) 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
}
.buttons .buttons-row .accept-button {
  width: 560px;
  background: linear-gradient(to bottom, #31fa2a 0%, #31fa2a 50%, #31f020 51%, #31f020 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
}
.buttons .buttons-row .accept-button:hover {
  background: linear-gradient(to bottom, rgba(49, 250, 42, 0.7) 0%, rgba(49, 250, 42, 0.7) 50%, rgba(49, 240, 32, 0.7) 51%, rgba(49, 240, 32, 0.7) 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
}

.custom-amount {
  background-color: #4FB0C6;
  width: 530px;
  padding: 15px;
  margin: 0 auto;
}
.custom-amount h2 {
  margin: 0;
  font-size: 2.5rem;
}
.custom-amount p {
  margin: 5px 0;
  font-size: 2.5rem;
  padding: 0 82px 0 0;
  text-align: right;
}